/* * * * * *
 * * * * * * Plans switcher * * * * * */

.b-pricing_switcher {
    margin-bottom: 60px;
}

.b-pricing_switcher_box {
    display: block;
    position: relative;
    width: 136px;
    height: 36px;
    margin: 0 auto;
    padding: 2px;
    color: #fff;
    overflow: hidden;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    border-radius: 18px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    cursor: pointer;
}

.b-pricing_switcher_box input {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    visibility: hidden;
}

.b-pricing_switcher_box_roll {
    display: block;
    position: relative;
    white-space: nowrap;
    letter-spacing: -5px;
    -webkit-transform: translate(0, 0);
    -moz-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
}

.b-pricing_switcher_box_roll:before {
    position: absolute;
    top: 0;
    right: 0;
    width: 32px;
    height: 32px;
    background: #fff;
    -webkit-box-shadow: 0 -2px 1px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0 -2px 1px rgba(0, 0, 0, .1);
    box-shadow: 0 -2px 1px rgba(0, 0, 0, .1);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    content: '';
}

.b-pricing_switcher_box_roll span {
    display: inline-block;
    position: relative;
    width: 132px;
    padding: 0 32px 0 0;
    font: 900 .875em/34px 'arial', sans-serif;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.b-pricing_switcher_box_roll span + span {
    width: 100px;
    padding: 0;
}

.b-pricing_switcher_box input:checked ~ .b-pricing_switcher_box_roll {
    -webkit-transform: translate(-100px, 0);
    -moz-transform: translate(-100px, 0);
    -ms-transform: translate(-100px, 0);
    transform: translate(-100px, 0);
}

.b-pricing_switcher_box input:checked ~ .b-pricing_switcher_box_roll:before {
    -webkit-box-shadow: 0 2px 1px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0 2px 1px rgba(0, 0, 0, .1);
    box-shadow: 0 2px 1px rgba(0, 0, 0, .1);
}

.b-pricing_switcher_box,
.b-pricing_switcher_box_roll,
.b-pricing_switcher_box_roll:before {
    -webkit-transition: all 300ms;
    -moz-transition: all 300ms;
    transition: all 300ms;
}


